@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500);
/*global style*/
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-wrapper {
  margin-top: 18%;
}
@media (min-width: 768px) {
  .table {
    margin-left: 15%;
  }
  .main-wrapper {
    margin-top: 7%;
  }
}

.wrapper {
  width: 90%;
  margin: 0 auto;
}

.add_task {
  width: 60%;
  margin: 0 auto;
}

@media (max-width: 1224px) {
  .add_task {
    width: 100%;
  }
  .btn-add {
    padding: 10px;
  }
}

.login {
  width: 60%;
  position: absolute;
  top: 23%;
  left: 21%;
}

@media (max-width: 768px) {
  .login {
    width: 100%;
    top: unset;
    left: unset;
    margin-top: 40%;
  }
}

